.form_form__2E981 {
  width: 270px;
  height: 100%;
  padding-top: 100px;
  margin-left: auto;
  margin-right: auto;

  display: flex;
  flex-direction: column;
  align-items: flex-start;
}

.form_formContainer__2WoPm {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  margin-bottom: 20px;
}

.form_label__1tNZe {
  display: flex;
  align-items: center;
  justify-content: space-between;
  width: 260px;
  font-size: 17px;
  margin-bottom: 10px;
}

.form_submitButton__3YFqW {
  padding: 10px 30px;
  background-color: var(--additional-color);
  font-size: 15px;
  border: none;
  border-radius: var(--border-radius);
