}

.form_submitButton__3YFqW:hover {
  background-color: var(--additional-color-hover);
}

.form_input__2uTPb {
  height: 25px;
  width: 120px;
  border: none;
  border-radius: var(--border-radius);
  transition: background-color 250ms ease;
}

.form_input__2uTPb:hover,
.form_input__2uTPb:focus {
  outline: none;
  background-color: var(--color-hover);
}

@media screen and (min-width: 600px) {
  .form_form__2E981 {
    width: 350px;
  }

  .form_input__2uTPb{
    width: 200px;
  }

  .form_label__1tNZe{
    width: 350px;
  }
